<script>
export default {
  name: "Widget",
  props: {
    widget: {
      type: Object,
      required: true,
    },
  },

  data() {
    return {
      isCollapsed: true,
      isExpanded: false,
      totalRows: 1,
      currentPage: 1,
      perPage: 5,
      pageOptions: [5, 10, 15, { value: 100, text: "Show a lot" }],
      sortBy: "",
      sortDesc: false,
      sortDirection: "asc",
      filter: null,
      filters: {
        search: undefined,
        manager: undefined,
        client: undefined,
        type: undefined,
        status: undefined,
      },
      filterOn: [],
      infoModal: {
        id: "info-modal",
        title: "",
        content: "",
      },
    };
  },

  computed: {
    widgetContent() {
      //   return this.$store.getters.TableFieldsById(this.widget.id);
      return this.$store.getters.WidgetContentById(this.widget.id);
    },
    tableContent() {
      return this.$store.getters.TableContentById(this.widget.table);
    },
    filteredItems() {
      let tableData = this.tableContent.data;

      // if (tableData !== "undefined" && this.filters["Search"] !== "undefined") {
      //   tableData = this.tableContent.data.filter((item) => {
      //     // bringing everything to lowercase to make comparisons more reliable
      //     // i.e., "Search" !== "search"

      //     // let flattenedItemContent = JSON.stringify(item);

      //     let flattenedItemContent = Object.keys(item).reduce(function(r, k) {
      //       return r.concat(k, item[k]);
      //     }, []);

      //     let itemContent = flattenedItemContent
      //       .toLowerCase()
      //       .replace(/[^a-z0-9\s]/gi, "")
      //       .replace(/s\b/gi, "");
      //     let searchQuery = this.filters["Search"]
      //       .toLowerCase()
      //       .replace(/[^a-z0-9\s]/gi, "")
      //       .replace(/s\b/g, "");

      //     // alert(searchQuery);

      //     if (searchQuery !== "") {
      //       this.resetVisible = true;
      //     }

      //     return itemContent.indexOf(searchQuery) !== -1;
      //   });
      // }

      if (tableData !== "undefined") {
        return tableData.filter((item) => {
          let keep = true;

          // This is a basic equality filter. What I did in the actual code was to have an object with filter functions for each key. If a key was missing, it defaulted to straight equality.
          this.fieldKeys.forEach((key) => {
            keep =
              keep &&
              (this.filters[key] === undefined ||
                item[key] === this.filters[key]);
          });

          return keep;
        });
      } else {
        return null;
      }
    },
    fieldKeys() {
      if (this.tableContent.data !== "undefined") {
        return Object.keys(this.tableContent.data[0]);
      } else return null;
    },
    options() {
      // This could be simplified if the select was it's own component.
      const options = {};
      this.fieldKeys.forEach((key) => {
        const vals = this.tableContent.data.map((item) => {
          return item[key];
        });

        // Cheap and efficient unique.
        options[key] = Array.from(new Set(vals));
      });

      return options;
    },
  },

  created() {
    this.$store.dispatch("getWidgetsContent");
    this.$store.dispatch("getWidgetTables");
  },

  mounted() {
    // Set the initial number of items
    this.totalRows = this.filteredItems
      ? this.filteredItems.length
      : this.tableContent.data.length;
  },

  methods: {
    info(item, index, button) {
      this.infoModal.title = `Row index: ${index}`;
      this.infoModal.content = JSON.stringify(item, null, 2);
      this.$root.$emit("bv::show::modal", this.infoModal.id, button);
    },
    resetInfoModal() {
      this.infoModal.title = "";
      this.infoModal.content = "";
    },
    onFiltered(filteredItems) {
      // Trigger pagination to update the number of buttons/pages due to filtering
      this.totalRows = filteredItems.length;
      this.currentPage = 1;
    },
  },
};
</script>

